
/**
 @Author: swls
 @ps: 操作栈
 */
class Stack {
  constructor(stackId, length){
    this._stackId = stackId || undefined
    this._stackArr = []
    // _stackArrUnloaded = []
    this._stackIndex = 0
    this._maxLength = length || 100
    this._stackStoreType = 1
  }

  add (objArr) {
    this._stackArr.splice(this._stackIndex, this._stackArr.length, objArr)
    this._stackIndex++
    if (this._stackArr.length >= this._maxLength) {
      this._stackArr.shift()
      this._stackIndex--
    }
    // console.log(this._stackArr)
  }

  undo () {//撤销
    if (this._stackIndex > 0) {
      let undoIndex = --this._stackIndex
      // this._stackIndex--;
      return this._stackArr[undoIndex]
    } else return undefined
  }

  redo () {//反撤销
    if (this._stackIndex < this._stackArr.length) {
      let redoIndex = this._stackIndex
      this._stackIndex++
      return this._stackArr[redoIndex]
    } else return undefined
  }

  len (number) {
    this._maxLength = number
  }

  get stackId () { return this._stackId }
  set stackId (value) { this._stackId = value }

  get stackArr () { return this._stackArr }
  set stackArr (value) { this._stackArr = value }

  get stackIndex () { return this._stackIndex }
  set stackIndex (value) { this._stackIndex = value }

  get maxLength () { return this._maxLength }
  set maxLength (value) { this._maxLength = value }

  get stackStoreType () { return this._stackStoreType }
  set stackStoreType (value) { this._stackStoreType = value }
}
export { Stack }



